import React, { Component } from "react";
import './Cart.css';
import ProductRow from "./ProductRow";
import { connect } from 'react-redux';
import EmptyCart from "./emptyCart";
import { Link } from 'react-router-dom';
import { checkLogIn_edit } from '../actions';


class Cart extends Component {
    cancelCart = () => {
        const idUser =  JSON.parse(localStorage.getItem("id"));
          this.props.checkLogIn_edit(idUser,[]);
          localStorage.setItem("cartUser", JSON.stringify([]));

    }
    render() {
        const idUser =  JSON.parse(localStorage.getItem("id"));

        const product =  JSON.parse(localStorage.getItem("cartUser"));
        console.log("cart : ", product);
        if(product === null || product.length === 0 || product.number === null){
            return (
                <EmptyCart/>
            )
        }
        const listProduct = product.map((item, index) => {
            return <ProductRow key={index} index={index} {...item} idUser={idUser}/>;
        });
        var sumPrice = (arr) =>{
            return arr.reduce((total, item) => {
                // if(item !== null)
                return total + (parseInt(item.price) * parseInt(item.number))
            }, 0)
        }
        const totalPrice = sumPrice(product);

        return (
            <div className="Cart">
                <h1>GI HNG</h1>
            <table className="totalTable" >
                    <tbody>
                        <tr>
                            <th>HNH NH</th>
                            <th>TN SN PHM</th>
                            <th>N GI</th>
                            <th>S LNG</th>
                            <th>THNH TIN</th>
                            <th>XA</th>
                        </tr>
                        {listProduct}
                    </tbody>
                </table>
                <div className="buttonGroup">
                    <button className="cartBtn hvr-fade" onClick={ () => this.cancelCart()}>HY N HNG</button>
                    <Link  to="/"> <button className="cartBtn light hvr-fade" >TIP TC MUA</button> </Link>
                </div>
                <div className="total">
                    <div>
                        <table>
                            <tbody>
                                <tr>
                                    <td>TNG TIN (CHA THU)</td>
                                    <td>{totalPrice} </td>
                                </tr>
                                <tr>
                                    <td>THU VAT (10%)</td>
                                    <td>{totalPrice * 0.1} </td>
                                </tr>
                                <tr>
                                    <td>TNG PHI THANH TON</td>
                                    <td>{Math.floor(totalPrice * 1.1)} </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="buttonGroup2">
                <Link  to="/checkout"> <button className="cartBtn hvr-fade">THANH TON</button> </Link>
                </div>
            </div>
        )
    }
}


const mapDispatchToProps = dispatch => {
    return {
        checkLogIn_edit: (id, cart) => dispatch(checkLogIn_edit(id, cart))
    }
}
const mapStateToProps = state => {
    return {
        stateReducers: state
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Cart);
