import React from 'react';
import { connect } from 'react-redux';
import { switchLogin } from '../actions';
import axios from 'axios';
import './modalListView.css';

class ModalRigs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            pw: "",
            pw2: "",
            name: "",
            phone: "",
            email: "",
            address:"",
            checkLogIn: false,
            usernameValid:"*",
            phoneValid:"*",
            emailValid:"*",
            pwValid:"*",
            pw2Valid:"*",
            nameValid:"*"
        }
    }
    getInputUser = (e) => {
        this.setState({
            username: e.target.value
        })
    }
    getInputPw = (e) => {
        this.setState({
            pw: e.target.value
        })
    }
    getInputPw2 = (e) => {
        this.setState({
            pw2: e.target.value
        })
    }
    getInputMail = (e) => {
        this.setState({
            email: e.target.value
        })
    }
    getInputPhone = (e) => {
        this.setState({
            phone: e.target.value
        })
    }
    getInputName = (e) => {
        this.setState({
            name: e.target.value
        })
    }
    getInputAddress = (e) => {
        this.setState({
            address: e.target.value
        })
    }
    goBack = () => {
        this.setState({
            redirect:true
        })
    }
    checkLength = (o, n, min, max) => {
        if (o.length > max || o.length < min) {
            switch(n) {
                case "phoneValid" : this.setState({
                    phoneValid : "S in thoi gm 10-11 k t"
                })
                break;
                case "usernameValid" : this.setState({
                    usernameValid : "Tn ng nhp phi t 6-16 k t"
                })
                break;
                case "pwValid" : this.setState({
                    pwValid : "Mt khu phi t 6-16 k t"
                })
                break;
                case "nameValid" : this.setState({
                    nameValid : "Xin nhp h tn"
                })
                break;
                case "emailValid" : this.setState({
                    emailValid : "Xin nhp Email"
                })
                break;
                default:
                    break;
            }
            
            return false;
        } else {
            switch(n) {
                case "phoneValid" : this.setState({
                    phoneValid : "*"
                })
                break;
                case "usernameValid" : this.setState({
                    usernameValid : "*"
                })
                break;
                case "pwValid" : this.setState({
                    pwValid : "*"
                })
                break;
                case "nameValid" : this.setState({
                    nameValid : "*"
                })
                break;
                case "emailValid" : this.setState({
                    emailValid : "*"
                })
                break;
                default:
                    break;
            }
            return true;
        }
    }
    checkMail = (regexp) => {
        if (!(regexp.test(this.state.email))) {
            this.setState({
                emailValid: "Xin nhp email theo mu linhnguyen@gmail.com"
            })
            return false;
        } else {
            this.setState({
                emailValid: "*"
            })
            return true;
        }
    } 
    checkPw = (pw,pw2) => {
        if(pw !== pw2) {
            this.setState({
                pw2Valid: "Mt khu khng khp, xin nhp li"
            }) 
            return false;
        }
        else {
            this.setState({
                pw2Valid: "*"
            }) 
            return true;
        }
    }
    addUser = () => {
        var emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
        var self = this;
        var valid = true;
        self.checkLength(self.state.phone, "phoneValid", 10, 11);
        self.checkLength(self.state.pw, "pwValid", 6, 16);
        self.checkLength(self.state.username, "usernameValid", 6, 16);
        self.checkLength(self.state.name, "nameValid", 1, 1000);
        self.checkMail(emailRegex);
        self.checkPw(self.state.pw,self.state.pw2)
        valid = valid && self.checkLength(self.state.phone, "phoneValid", 10, 11) && self.checkLength(self.state.pw, "pwValid", 6, 16) 
                      && self.checkLength(self.state.username, "usernameValid", 6, 16) && self.checkLength(self.state.name, "nameValid", 1, 1000) 
                      && self.checkMail(emailRegex) && self.checkPw(self.state.pw,self.state.pw2)
    

        if (valid) {
            axios.post('https://5ca5c51d3a08260014278a74.mockapi.io/usersShop',
            {
                name: self.state.name, id: Date.now(), phone: self.state.phone, email: self.state.email,
                username: self.state.username, pw: self.state.pw, address:self.state.address, status:true, cart:"{}", order: "{}", dateOrder: "", statusOrder: "Ordered"
            }
            )
            alert("ng k thnh cng !");
            this.setState({
                redirect:true
            })
        }
    }
    render() {
        return (
            <div className="modal_box">
                <div className="modal_header">
                    <p>To ti khon Green shop</p>
                    <div>
                        Bn  l thnh vin? <button type="button" onClick={() => this.props.switchLogin(true)}>ng nhp</button> ti y.
                                </div>
                </div>
                <div className="modal_body">
                    <div className="modal_input">
                        <div className="login_input">
                            <label >H tn <span> {this.state.nameValid}</span> </label>
                            <input className="input-with-status__input" type="text" placeholder="Vui lng nhp h v tn ca bn" onChange={this.getInputName}/>
                        </div>
                        <div className="login_input">
                            <label>Email <span>{this.state.emailValid}</span> </label>
                            <input className="input-with-status__input" type="text" placeholder="Vui lng nhp email ca bn" onChange={this.getInputMail}/>
                        </div>
                        <div className="login_input">
                            <label>i ch </label>
                            <input className="input-with-status__input" type="text" placeholder="Vui lng nhp email ca bn" onChange={this.getInputAddress}/>
                        </div>
                        <div className="login_input">
                            <label>Mt khu <span>{this.state.pwValid}</span></label>
                            <input className="input-with-status__input" type="password" placeholder="Vui lng nhp mt khu ca bn" onChange={this.getInputPw} />
                        </div>
                        <div className="login_input">
                            <label>Nhp li mt khu <span>{this.state.pw2Valid}</span></label>
                            <input className="input-with-status__input" type="password" placeholder="Vui lng nhp li mt khu ca bn" onChange={this.getInputPw2} />
                        </div>
                    </div>
                    <div className="modal_btn">
                        <div className="login_input">
                            <label>S in thoi <span> {this.state.phoneValid}</span> </label>
                            <input className="input-with-status__input" type="text" placeholder="Vui lng nhp s in thoi ca bn" onChange={this.getInputPhone}/>
                        </div>
                        <div className="login_input">
                            <label >Tn ng nhp <span> {this.state.usernameValid}</span> </label>
                            <input className="input-with-status__input" type="text" placeholder="Vui lng nhp tn ng nhp ca bn" onChange={this.getInputUser}/>
                        </div>
                        <button type="button" className="mod-button" onClick={this.addUser}>ng k</button>
                        <p>Hoc, ng k bng</p>
                        <button type="button" className="mod-button mod-login-fb " onClick={() => alert("Chc nng ang hon thin")}> <i className="fab fa-facebook-f"></i> &emsp; Facebook</button>
                        <button type="button" className="mod-button mod-login-google" onClick={() => alert("Chc nng ang hon thin")}> <i className="fab fa-google-plus-g"></i> &emsp; Google</button>
                    </div>
                </div>
            </div>
        );
    }
}


const mapDispatchToProps = dispatch => {
    return {
        switchLogin: (name) => dispatch(switchLogin(name))
    }
}
export default connect(null , mapDispatchToProps) (ModalRigs);
